<template>
  <div :style="{width: img_width + 'rem'}" class="cover_box" @click="to_this_works" @mouseenter="hover" @mouseleave="hover_stop">
    <div
      :style="{ width: img_width+ 'rem', height: img_height+ 'rem'}"
      class="img_box"
    >
      <img
        :id="cover_title+'_img'"
        :src="cover_src"
        :style="{ width: img_width+ 'rem', height: img_height+ 'rem' }"
        class="cover_img"
      />
    </div>

    <div :id="cover_title+'_title'" class="cover_title">{{ cover_title }}</div>

    <div class="tag_list">
      <div v-for="(tag, index) of tag_list" id="tag" :key="tag" class="tag">{{tag}}{{ index === tag_list.length - 1 ? "" : ", " }}
      </div>
    </div>
  </div>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.img_box {
  overflow: hidden;
}

img.cover_img {
  object-fit: cover;
}

div.cover_title {
  display: inline-block;
  padding-top: 20px;
  color: #251714;
  font-size: 22px;
  text-transform: uppercase;
  line-height: 1;
}

div.tag_list {
  padding-top: 5px;
  display: flex;
  flex-direction: row;
}

div.tag {
  display: inline-block;
  font-size: 16px;
  text-transform: uppercase;
  line-height: 1;
  white-space: pre-wrap;
}
</style>


<script>
import anime from "../../../../modules/anime";


export default {
  name: "WorksCover",
  props: [
    "id",
    "cover_name",
    "img_width",
    "img_height",
    "cover_src",
    "cover_title",
    "tag_list",
    "cover_intro"
  ],
  data() {
    return {
    };
  },
  beforeMount() {
  },
  mounted() {
  },
  destroyed() {
  },
  methods: {
    to_this_works() {
      this.$router.push("/Works/" + this.id);
    },
    hover(){
      let that = this
      anime({
        targets: document.getElementById(that.cover_title+"_img"),
        scale: 1.2,
        duration: 200,
        easing: "linear"
      });
      document.getElementById(that.cover_title+"_title").style.textDecoration = "underline"
      document.getElementById(that.cover_title+"_title").style.textDecorationColor = "#efcd24"
    },

    hover_stop(){
      let that = this
      anime({
        targets: document.getElementById(that.cover_title+"_img"),
        scale: 1,
        duration: 200,
        easing: "linear"
      });

      document.getElementById(that.cover_title+"_title").style.textDecoration = "unset"

    }
  },
};
</script>



