<template>
  <div id="works_page" class="works_page">
  <div  v-if="global.mode === 0">
    <div class="title_part">
      <div :class="'title_'+global.lang">{{ global.lang_pack.works }}</div>
    </div>

    <div class="works_list_part">
      <div v-for="(item,index) in id_list" class="works">
        <WorksCover
          v-if="config_list[index]"
          :id="id_list[index]"
          :cover_src="'/static/works/'+id_list[index]+'/cover.png'"
          :cover_title="config_list[index].cover_title"
          :tag_list="config_list[index].tag_list"
          :cover_intro="config_list[index].cover_intro"
          img_height=350
          img_width=450
        ></WorksCover>
      </div>
    </div>
  </div>

    <div v-if="global.mode === 1" class="mobile_body">
      <div class="title_part">
        <div :class="'title_'+global.lang">{{ global.lang_pack.works }}</div>
      </div>

      <div class="works_list_part">
        <div v-for="(item,index) in id_list" class="works">
          <WorksCover
            v-if="config_list[index]"
            :id="id_list[index]"
            :cover_src="'/static/works/'+id_list[index]+'/cover.png'"
            :cover_title="config_list[index].cover_title"
            :tag_list="config_list[index].tag_list"
            :cover_intro="config_list[index].cover_intro"
            img_height=544.44
            img_width=700
          ></WorksCover>
        </div>
      </div>
    </div>

    <Footer></Footer>
    <Menu></Menu>
    <LangTransfer></LangTransfer>
  </div>
</template>

<style lang="css" scoped>
div.works_page {
  background: #f7f7f7;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  overflow-x: hidden;
}

div.works_generally_intro {
  margin-top: 50px;
  font-size: 20px;
  margin-left: 11.98%;
  margin-right: 11.98%;
}

div.works_list_part {
  margin-top: 100rem;
  margin-left: 11.98%;
  margin-right: 11.98%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

div.works {
  margin-bottom: 50px;
}

/*手机版样式*/
@media screen and (max-width: 540px) {
  div.works_list_part {
    margin-top: unset;
    margin-left: unset;
    margin-right: unset;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  div.works {
    margin-bottom: unset;
  }
}
</style>

<script>
import global from "../global";
import Menu from "../../components/menu/Menu";
import WorksCover from "../../components/works/cover/Cover";
import LangTransfer from "../../components/lang_transfer/LangTransfer"
import Footer from "../../components/footer/Footer"

//export default
export default {
  name: "Works",

  data() {
    return {
      global: global,
      config_list: [],
      // 添加作品要在这里添加对应作品的名字
      id_list: [
        // "DYT_HOTEL_QINGDAO",
        // "MGM_HOTEL_QINGDAO",
        "ATLANTIS_SANYA",
        // "YANGON_YOMA_CENTRAL_MYANMAR",
        // "MGM_HOTEL_SHENZHEN",
        // "LINGNAN_3E_NEW_WORLD_CHINA_GUANGZHOU",
        // "SIIC_OFFICE_TOWER_QINGDAO",
        // "ONE_SHENNAN_SHENZHEN",
        // "HENGGANG_SPORTS_CENTER_SHENZHEN",
        // "GRAND_HYATT_SHENZHOU_PENINSULA_SANYA",
        // "BASIS_INTERNATIONAL_SCHOOL_HUIZHOU",
        // "NEW_WORLD_ZENG_CHENG_GUANGZHOU"
      ],
    };
  },
  components: {
    Menu,
    WorksCover,
    Footer,
    LangTransfer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.refresh_windows_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.refresh_windows_size();
      })();
    };
    Menu.methods.change_logo_color("#251714", "#efcd24", 1000);
    this.get_covers();
  },
  destroyed() {
  },
  watch: {
    "global.lang":function (i){
      this.get_covers()
    }
  },
  methods: {
    get_covers() {
      let that = this
      that.config_list = []
      that.id_list.forEach((id) => {
        that.config_list.push({
          id: id,
          cover_title: require("../../../static/works/" +
            id +
            "/config.js")[
          "works_content_" + global.lang
            ].cover_title,
          tag_list: require("../../../static/works/" + id + "/config.js")[
          "works_content_" + global.lang
            ].tag_list
        });
      });
    },
  },
};
</script>



